import React, {  useEffect, useState } from 'react';
import { getOther } from '../redux/actions/app'
import Title from '../component/Title'
import Preorder from '../component/Preorders'
import Error from '../component/Error'

const Main = React.memo(
    (props) =>{
        let [preorders, setPreorders] = useState([]);
        useEffect(async ()=>{
            let data = await getOther({name: 'Предзаказы'})
            await setPreorders(data)
            if(window.rerun!=undefined)
                await window.rerun()
        },[]);
        return (
            <>
            <Title title="Предзаказы"/>
            {preorders.length>3?
                preorders.map((element, idx) => {
                    if(idx!==0&&(idx+1)%4===0) {
                        return <Preorder type={1} history={props.history} items={[preorders[idx-3], preorders[idx-2], preorders[idx-1], preorders[idx]]}/>
                    }
                    else if(idx+1===preorders.length) {
                        if((idx+1)%3===0)
                            return <Preorder type={1} history={props.history} items={[preorders[idx-2], preorders[idx-1], preorders[idx]]}/>
                        else if((idx+1)%2===0)
                            return <Preorder type={1} history={props.history} items={[preorders[idx-1], preorders[idx]]}/>
                        else
                            return <Preorder type={1} history={props.history} items={[preorders[idx]]}/>
                    }
                })
                :preorders.length>1?
                    <Preorder type={1} history={props.history} items={[preorders[0], preorders[1], preorders[2]]}/>
                    :preorders.length>1?
                        <Preorder type={1} history={props.history} items={[preorders[0], preorders[1]]}/>
                        :preorders.length>0?
                            <Preorder type={1} history={props.history} items={[preorders[0]]}/>
                            :<Error/>
            }
            </>
        );
    })

export default Main;
